
import Vue from "vue";
import { PlusCircleIcon, MoonIcon, SunIcon } from "vue-feather-icons";

export default Vue.extend({
  props: {
    handleOpenModal: Function,
    handleSearch: Function,
    handleMode: Function,
    darkMode: Boolean,
  },
  data() {
    return {
      searchValue: "",
    };
  },
  components: { PlusCircleIcon, MoonIcon, SunIcon },
  watch: {
    searchValue(ev) {
      this.handleSearch(ev);
    },
  },
});
